<template>
  <div class="blogshow">
    <mavon-editor
      class="markdown"
      v-model="mdContent"
      :subfield="settingMd.subfield"
      :defaultOpen="settingMd.defaultOpen"
      :toolbarsFlag="settingMd.toolbarsFlag"
      :boxShadow="settingMd.boxShadow"
      :previewBackground="settingMd.previewBackground"
      :navigation="false"
    />
  </div>
</template>

<script>
import requestApi from '../api/requestApi'

export default {
  name: "Bolgshow",
  data() {
    return {
      settingMd: {
        subfield: false, // 单双栏模式
        defaultOpen: "preview", // 默认展示
        toolbarsFlag: false,
        boxShadow: false,
        previewBackground: "#fff",
        navigation: false, // 默认展示目录
        toolbars: {},
      },
      mdContent: ''
    };
  },
  mounted() {
    this.renderMd()
  },
  methods: {
    renderMd() {
      let md = this.$route.params.md
      requestApi.fetch(`blogMd/${md}.md`).then((res) => {
        this.mdContent = res.data
      })
    }
  },
  watch: {
    $route() {
      this.renderMd()
    }
  },
  beforeRouteLeave(to, from, next) {
    if(to.fullPath === '/blog') {
      this.$store.dispatch('get_blog_display_now', '');
    }
    next()
  }
}
</script>

<style lang="less">
.blogshow {
  margin-top: 88px;
  // border: 2px solid #555;
  // border-radius: 10px;

  .markdown-body {
    margin: 0 10px;
    border-color: #fff;

    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 20px 0 15px;
      font-family: "Lato", "PingFang SC", "Microsoft YaHei", sans-serif;
      // line-height: 1.5;
      color: #222;
      // background: #fff;
      border-bottom: #fff;
    }

    p {
      margin-bottom: 25px;
      font-family: "Lato", "PingFang SC", "Microsoft YaHei", sans-serif;
      color: #333;
      font-size: 16px;
      line-height: 2;
    }

    @media screen and (max-width: 950px) {
      pre {
        width: calc(100vw - 90px);
        // width: 100vw;
        min-width: 270px;
      }
      blockquote {
        width: calc(100vw - 90px);
        // width: 100vw;
        min-width: 270px;
      }            
    }
  }
}
</style>